.blackBorder {
    border: 1px solid black;
}

table {
    border-collapse: collapse;
    width: 100%;
}

thead {
    font-weight: bold;
    background-color: grey;
    color: white;
}

td,
th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
    font-weight: normal;
}

th:nth-child(1) {
    width: 300px;
}

th:nth-child(even) {
    width: 300px;
}

.firstClm {
    width: 300px;
    background-color: grey;
    color: white;
}
